* {
  box-sizing: border-box;
  max-width: 100vw;
  font-family: "Netflix Sans" !important;
}

.App {
  text-align: center;
  /* background-color: #ececec; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  /* color: rgb(29, 29, 29); */
}

h1.home-profile-description {
  margin-top: 0px;
}

.home-profile-description {
  margin-bottom: 45.614px;
}

p.home-profile-description {
  font-size: 30px;
}

p.home-liked-stuff-description {
  margin-bottom: 72px;
  font-size: 30px;
}

.container-home-media-cards {
  display: flex !important;
  gap: 36px;
  flex-wrap: wrap;
  justify-content: center !important;
}

.home-footer {
  font-size: 12px;
  margin-top: 48px;
}

.card-component {
  flex-wrap: wrap !important;
}

.dropdown-component {
  box-shadow: none !important;
}

a {
  text-decoration: none;
}

.container-home-page {
  width: 1200px !important;
}

.images-carousel-element {
  justify-content: center;
  place-content: center;
}

.nav-bar button {
  font-weight: bolder;
}

.MuiFormControlLabel-root {
  margin-right: 0px !important;
}

.container-social-media {
  display: flex !important;
  gap: 12px !important;
  margin-top: 48px;
  margin-bottom: 48px;
  align-content: center !important;
  place-items: center !important;
  justify-content: center !important;
  align-items: center !important;
  flex-wrap: wrap !important;
  line-height: 2px !important;
}

/* Mobile S */

@media (min-width: 320px) and (max-width: 374px) {
  .button-theme {
    display: none !important;
  }

  .container-home-media-cards {
    gap: 36px;
  }
}

/* Mobile M */

@media (min-width: 375px) and (max-width: 424px) {
  #meme {
    width: 95% !important;
  }

  .button-theme {
    position: relative !important;
    left: 142px;
    bottom: 49px;
  }

  .container-home-media-cards {
    gap: 36px;
  }

  .MuiSwitch-root {
    width: 36px !important;
  }

  .MuiSwitch-track {
    display: none !important;
  }

  .MuiSwitch-switchBase {
    transform: none !important;
  }

  .MuiFormControlLabel-root {
    width: 0px !important;
  }
}

